<template>
  <div id="singer-detail">
    <div class="music-container">

      <h2>歌手介绍</h2>
      <div class="singer-header">
        <img
          v-lazy="picUrl"
          alt=""
        >
          <div class="info">{{briefDesc}}</div>
      </div>

      <h2>热门歌曲</h2>
      <div class="song-header">
        <div class="id nav">序号</div>
        <div class="name nav">歌名</div>
        <div class="songer nav">歌手</div>
        <div class="time nav">时长</div>
        <div class="album nav">专辑</div>
      </div>
      <div class="song-container">
        <song-item
          v-for="(item, index) in songs"
          :key="index"
          :index="index"
          :songId="item.id"
          :alId="item.al.id"
          :arId="item.ar[0].id"
          :songName="item.name"
          :alName="item.al.name"
          :arName="item.ar[0].name"
          :dt="item.format_dt"
        />
      </div>

      <h2 v-if="show">MV</h2>
      <div class="mv-container">
        <mv-item
          class="item"
          v-for="(item, index) in mv"
          :key="index"
          :mvId="item.id"
          :cover="item.imgurl"
          :name="item.name"
          :artistName="item.artistName"
          :artists="item.artists"
          :playCount="item.playCount"
        >
          </mv-item>
      </div>
    </div>
  </div>
</template>
<script>
import {
  req_singerAlbum,
  req_singerMv,
  req_singerDesc,
} from "../network/singer/index.js";
import { req_search_singer } from "../network/search/index.js";
import tools from "../tools/formatData.js";
import songItem from "../components/item/songItem";
import mvItem from "../components/item/mvItem";
export default {
  name: "SingerDetail",
  components: {
    songItem,
    mvItem,
  },
  data() {
    return {
      id: this.$route.query.id,
      name: this.$route.query.name,
      picUrl: "",
      mv: [],
      briefDesc: "",
      songs: [],
      show: false,
    };
  },
  watch: {
    songs: function() {
      this.songs.forEach((e, index, arr) => {
        let value = tools._msTos(e.dt);
        arr[index].format_dt = value;
      });
    },
     mv: function() {
        this.mv.forEach((e, index, arr) => {
          let value = tools._tranNumber(e.playCount, 2);
          arr[index].playCount = value;
        });
     },
  },
  created() {
    this.getSingerMv();
    this.getSingerDesc();
    this.getSingerAlbum();
    this.search();
  },
  methods: {
    getSingerMv() {
      req_singerMv(this, { id: this.id })
        .then(res => {
          this.mv = res.mvs.slice(0, 8);
        })
        .catch(error => {
          console.log(error);
        });
    },
    getSingerDesc() {
      req_singerDesc(this, { id: this.id })
        .then(res => {
          this.briefDesc = res.briefDesc;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getSingerAlbum() {
      req_singerAlbum(this, { id: this.id })
        .then(res => {
          this.songs = res.songs;
          this.show = true;
        })
        .catch(error => {
          console.log(error);
        });
    },
    search() {
      req_search_singer(this, {
        keywords: this.name,
        type: 100,
      }).then(res => {
        this.picUrl = res.result.artists[0].picUrl;
      });
    },
  },
};
</script>
<style lang="scss">
@import "../assets/scss/music.scss";
#singer-detail {
  .music-container {
    h2 {
      text-align: center;
      margin-top: 20px;
      margin-bottom: 30px;
    }
    .singer-header {
      height: 300px;
      margin-top: -30px;
      @include flex(flex-start);
      img {
        width: 200px;
        height: 200px;
        border-radius: 50%;
      }
      .info {
        widows: 600px;
        margin-left: 30px;
        height: 200px;
        padding-top: 30px;
        box-sizing: border-box;
        @include font(16px, 30px, $colorC);
        @include fontDHidden(6);
      }
    }
    .song-header {
      height: 30px;
      @include flex();
      border: 1px solid #f5f5f5;
      background-color: #f5f5f5;
      .nav {
        @include font(18px, 30px, $colorC);
        text-align: center;
        border-right: 1px solid #ffffff;
        &:last-child {
          border: none;
        }
        &:first-child {
          width: 40px;
        }
        &:nth-child(n + 2) {
          flex: 1;
        }
      }
    }
    .song-container {
      margin-bottom: 70px;
    }
    .mv-container {
      @include flex();
      flex-wrap: wrap;
    }
  }
}
</style>